.customize-theme-container {
    transition: 0.5s linear;
    animation: slideshow 0.2s ease-out;
}
.blur {
    opacity: 0.5;
    background-color: white;
    height: 100%;
    width: 100%;

}
@keyframes slideshow {
    0% {
        transform: translateX(100px);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

